<template>
  <div id="warp">
      <h2>作品展示页</h2>
      <div class="warp">
        <div  class="img" v-for="(item,index) in list" @click="showimg(index)">
            <img class="img1" :src="item" alt="">
        </div>       
      </div>
      <div class="showimg" v-if="show" @click="show = !show">
          <img class="currentimg" :src="imgs" alt="">
      </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      show:false,
      imgs:'',
      list:[
        "static/img/1.jpg",
        "static/img/2.jpg",
        "static/img/3.jpg",
        "static/img/4.jpg"
      ]
    }
  },
  methods:{
    showimg(index){
      console.log(index)
      this.show = !this.show
      this.imgs = this.list[index]
    }
  }
}
</script>

<style scoped>
#warp{
  width: 1000px;
  margin:100px auto;
}
h2{
  font-size:50px;
  font-weight: 700;
  text-align:center;
}
.warp{
  margin-top:80px;
  width: 100%;
  display: flex;
  flex-wrap:wrap;
  justify-content: space-between;
}
.img{
  width: 32%;
  margin-bottom:50px;
}
.img1{
  width: 100%;
  height:480px;
  vertical-align: top;
}
.showimg{
      width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
}
.currentimg{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
</style>



